export default "<template>\n  <div>\n    <div class=\"preview\">\n      <fd-split-button\n        :compact=\"compact\"\n        :state=\"state\"\n        :styling=\"styling_\"\n        :type=\"type_\"\n        :icon=\"icon_\"\n        >{{ text }}</fd-split-button\n      >\n    </div>\n    <fd-form-item label=\"Text\">\n      <fd-input v-model=\"text\" placeholder=\"Enter Button Text\" />\n    </fd-form-item>\n    <fd-form-item>\n      <fd-form-label>Styling</fd-form-label>\n      <fd-select v-model=\"styling\">\n        <option value=\"\">none</option>\n        <option value=\"emphasized\">emphasized</option>\n        <option value=\"light\">light</option>\n      </fd-select>\n    </fd-form-item>\n    <fd-form-item>\n      <fd-form-label>Type</fd-form-label>\n      <fd-select v-model=\"type\">\n        <option value=\"\">none</option>\n        <option value=\"standard\">standard</option>\n        <option value=\"positive\">positive</option>\n        <option value=\"negative\">negative</option>\n        <option value=\"medium\">medium</option>\n      </fd-select>\n    </fd-form-item>\n    <fd-form-item>\n      <fd-form-label>Icon</fd-form-label>\n      <fd-select v-model=\"icon\">\n        <option value=\"\">none</option>\n        <option value=\"cart\">cart</option>\n        <option value=\"menu\">menu</option>\n      </fd-select>\n    </fd-form-item>\n    <fd-form-item>\n      <fd-form-label>State</fd-form-label>\n      <fd-select v-model=\"state\">\n        <option value=\"normal\">normal</option>\n        <option value=\"selected\">selected</option>\n        <option value=\"disabled\">disabled</option>\n      </fd-select>\n    </fd-form-item>\n    <fd-form-group inline>\n      <fd-form-item label=\"Compact\">\n        <fd-checkbox v-model=\"compact\" />\n      </fd-form-item>\n    </fd-form-group>\n  </div>\n</template>\n\n<script>\nexport default {\n  computed: {\n    styling_() {\n      return this.styling === \"\" ? null : this.styling;\n    },\n    icon_() {\n      return this.icon === \"\" ? null : this.icon;\n    },\n    type_() {\n      return this.type === \"\" ? null : this.type;\n    }\n  },\n  data() {\n    return {\n      type: \"\",\n      state: \"normal\",\n      styling: \"\",\n      compact: false,\n      text: \"Button with Text\",\n      icon: \"\"\n    };\n  }\n};\n</script>\n\n<style scoped>\n.preview {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding-bottom: 20px;\n  border-bottom: 1px solid #efefef;\n  margin-bottom: 20px;\n}\n</style>"