export default function (Component) {
Component.options.__docs = "<p><code>fd-menu</code> currently has a limited API to control the highlighting-state. At this moment only two methods are exposed:</p>\n<ol>\n<li><code>highlightNextItem()</code>: Highlights the next menu item.</li>\n<li><code>highlightPreviousItem()</code>: Highlights the previous menu item.</li>\n</ol>\n<p>You should rarely need to manually call those methods. They are mainly used to support keyboard navigation.</p>\n"
}