export default function (Component) {
Component.options.__docs = "<p><code>fd-split-button</code> is a composition of two other components:</p>\n<ol>\n<li><code>fd-split-button-action</code>: The <em>main</em> area on the left. <code>fd-split-button</code> will emit a <code>click</code>-event when the action-area is clicked.</li>\n<li><code>fd-split-button-auxiliary</code>: The area on the right. <code>fd-split-button</code> will emit a <code>click:auxiliary</code>-event when the auxiliary-area is clicked. You should show a popover/menu in when this event is emitted.</li>\n</ol>\n"
}